module.exports = {
	/**
	 * 
	 */
	data: [],
	/**
	 * 
	 */
	error: [],
	/**
	 * 
	 */
	defaultErrorMsg: {
		regex: ':attribute',
		require: ':attribute',
		number: ':attribute',
		chn: ':attribute',
		chnNum: ':attribute',
		chnOrNum: ':attribute',
		alphaLine: ':attribute',
		landline: ':attribute',
		mobile: ':attribute',
		alphaNum: ':attribute',
		email: ':attribute',
		zipCode: ':attribute',
		min: ':attribute:rule',
		max: ':attribute:rule',
		length: ':attribute:rule',
		notbetween: ':attribute  :1 - :2 ',
		in: ':attribute:rule',
		notIn: ':attribute:rule',
		different: ':attribute:rule',
		gt: ':attribute:rule',
		egt: ':attribute:rule',
		elt: ':attribute:rule',
		lt: ':attribute:rule',
		eq: ':attribute:rule',
		notEq: ':attribute:rule'
	},
	/**
	 * 
	 */
	alias: {
		'>': 'gt',
		'>=': 'egt',
		'<': 'lt',
		'<=': 'elt',
		'=': 'eq', 'same': 'eq',
		'!=': 'notEq', '<>': 'notEq'
	},
	/**
	 * 
	 */
	getAlias(value, compare = (a, b) => a === b) {
		var result = Object.keys(this.alias).find(k => compare(this.alias[k], value))
		if (typeof result == 'undefined') {
			var result = this.alias[value];
		}
		return result;
	},
	
	/**
	 * 
	 * @param {Object} data
	 * @param {Object} param
	 */
	check(data, param) {
		// 
		this.error = [];
		this.data = data;
		
		for (var i = 0; i < param.length; i++) {
			// name
			var name = param[i].name;
			// 
			var nameChn = param[i].nameChn;
			// 
			var value = data[name];
			// 
			var rules = param[i].rules;
			// 
			var errorMsg = param[i].errorMsg;

			for (let index in rules) {
				if (rules.indexOf('require') < 0) {
					// 
					if (!this.checkResult('require', value)) {
						continue;
					}
				}
				
				if (!this.checkResult(rules[index], value)) {
					// 
					this.buildError(name, nameChn, rules[index], errorMsg);
				}
			}
		}
		
		// 
		return JSON.stringify(this.error) == '[]' ? true : false;
	},
	
	/**
	 * 
	 */
	buildError(name, nameChn = '', rule, errorMsg = '') {
		// rule 
		if (typeof rule == 'object') {
			// 
			var ruleName = rule[0];
			if (rule[1]) {
				var ruleValue = rule[1];
			} else {
				var ruleValue = '';
			}
		} else {
			// 
			if (rule.indexOf(':') > -1) {
				var ruleArray = rule.split(':');
				var ruleName = ruleArray[0];
				var ruleValue = ruleArray[1];
			} else {
				var ruleName = rule;
				var ruleValue = '';
			}
		}
		
		if (errorMsg !== '') {
			// 
			if (!errorMsg[ruleName]) {
				// 
				if (typeof this.getAlias(ruleName) != 'undefined') {
					// 
					this.error.push(errorMsg[this.getAlias(ruleName)]);
					return;
				}
			} else {
				// 
				this.error.push(errorMsg[ruleName]);
				return;
			}
		}
		
		// 
		if (String(ruleValue).indexOf(',') > -1) {
			// 
			var ruleValueArray = ruleValue.split(',');
			ruleValueArray.push('');
		} else {
			var ruleValueArray = ['', '', ''];
		}
		
		// 
		var defaultErrorMsg = this.defaultErrorMsg[ruleName];
		defaultErrorMsg = defaultErrorMsg.replace(/(:attribute|:rule|:1|:2)/g, ($0, $1) => {
			return {
				// 
				':attribute': nameChn != '' ? nameChn : name,
				':rule': ruleValue,
				':1': ruleValueArray[0],
				':2': ruleValueArray[1],
			}[$1];
		});
		
		// 
		// this.error[name] = [defaultErrorMsg];
		this.error.push(defaultErrorMsg);
	},
	
	getError() {
		return this.error;
	},
	getErrorFirst() {
		return this.error[0] || null;
	},
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	checkResult(rule, value) {
		switch (rule) {
			case 'require':
				// 
				return !(!value || String(value).length < 1);
				break;
			case 'number':
				// 
				return this.regex(/^-?[1-9][0-9]?.?[0-9]*$/, String(value));
				break;
			case 'integer':
				// 
				return this.regex(/(^[1-9]\d*$)/, String(value));
				break;
			case 'float':
				// 
				return this.regex(/^(-?\\d+)(\\.\\d+)?$/, String(value));
				break;
			case 'chn':
				// 
				return this.regex(/^[\u4e00-\u9fa5]+$/u, decodeURI(value));
				break;
			case 'chnNum':
				// 
				return this.regex(/^[0-9\u4e00-\u9fa5]+$/u, decodeURI(value));
				break;
			case 'chnOrNum':
				// 
				return this.regex(/^[\u4e00}-\u9fa5]+|[0-9]+$/u, decodeURI(value));
				break;
			case 'alphaLine':
				// 
				return (value.slice(0, 1) != '_' && value.slice(-1) != '_' && this.regex(/^[A-Za-z\_]+$/, value));
				break;
			case 'landline':
				// 
				return this.regex(/^(\+\d{2}-)?0\d{2,3}-\d{7,8}|(\+\d{2}-)?\(0\d{2,3}\)\d{7,8}$/, decodeURI(value));
				break;
			case 'mobile':
				// 
				return this.regex(/^1[345789]\d{9}$/, parseInt(decodeURI(value)));
				break;
			case 'alphaNum':
				// 
				return this.regex(/^[a-zA-Z]+|[0-9]+$/, value);
				break;
			case 'email':
				// 
				return this.regex(/^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/, value);
				break;
			case 'zipCode':
				// 
				return this.regex(/^[0-9]{6}$/, value);
				break;
			default:
				// 
				if (typeof rule == 'string' && rule.indexOf(':') > -1) {
					// 
					var ruleArr = rule.split(':');
				}
				if (typeof rule == 'object') {
					// 
					var ruleArr = rule;
				}
				if (typeof this[ruleArr[0]] != 'function') {
					// 
					ruleArr[0] = this.getAlias(ruleArr[0]);
				}
				return this[ruleArr[0]](ruleArr[1], value);
		}
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	regex(rule, value) {
		var pattern = new RegExp(rule);
		return pattern.test(value);
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	min(rule, value) {
		return String(value).length >= rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	max(rule, value) {
		return String(value).length <= rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	length(rule, value) {
		var length = String(value).length;
		if (String(rule).indexOf(',') > -1) {
			// 
			[min, max] = rule.split(',');
			return length >= min && length <= max;
		} else {
			// 
			return length == rule;
		}
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	notbetween(rule, value) {
		if (String(rule).indexOf(',') > -1) {
			[min, max] = rule.split(',');
			return value < min || value > max;
		}
		return false;
	},
	
	/**
	 * 
	 * @param {Object} rule a,b,c
	 * @param {Object} value
	 */
	in(rule, value) {
		return rule.split(',').includes(value);
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	notIn(rule, value) {
		return !rule.split(',').includes(value);
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	identical(rule, value) {
		return this.data[rule] === value;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	different(rule, value) {
		return this.data[rule] != value;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	gt(rule, value) {
		return Number(value) > rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	egt(rule, value) {
		return Number(value) >= rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	elt(rule, value) {
		return Number(value) <= rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	lt(rule, value) {
		return Number(value) < rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	eq(rule, value) {
		return Number(value) == rule;
	},
	
	/**
	 * 
	 * @param {Object} rule
	 * @param {Object} value
	 */
	notEq(rule, value) {
		return Number(value) != rule;
	}
};
